import React, { useState } from "react";
import { Link as ScrollLink } from "react-scroll";
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
  Box,
  Menu,
  MenuItem,
  Button
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import './Navbar.css';

const NavigationBar = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [resumeAnchorEl, setResumeAnchorEl] = useState(null);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleResumeMenuOpen = (event) => {
    setResumeAnchorEl(event.currentTarget);
  };

  const handleResumeMenuClose = () => {
    setResumeAnchorEl(null);
  };

  const navItems = [
    { id: "home", label: "Home" },
    { id: "summary", label: "Summary" },
    { id: "skills", label: "Skills" },
    { id: "experience", label: "Experience" },
    { id: "certifications", label: "Certifications" },
    { id: "education", label: "Education" },
    { id: "recognition", label: "Awards" },
    { id: "contact", label: "Contact" },
  ];

  const resumeItems = navItems.filter(item => 
    ["summary", "skills", "experience", "certifications", "education", "recognition"].includes(item.id)
  );

  const drawer = (
    <List>
      {navItems.map(({ id, label }) => (
        <ListItem 
          key={id} 
          component={ScrollLink}
          to={id}
          smooth={true}
          duration={400}
          offset={-70}
          onClick={handleDrawerToggle}
        >
          <ListItemText primary={label} />
        </ListItem>
      ))}
    </List>
  );

  return (
    <>
      <AppBar position="sticky" color="primary" elevation={1}>
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Vishal Biyani
          </Typography>
          
          {/* Desktop Menu */}
          <Box sx={{ display: { xs: 'none', sm: 'flex' } }}>
            <Button 
              color="inherit" 
              onClick={handleResumeMenuOpen}
              sx={{ mr: 2 }}
            >
              Resume
            </Button>
            <Menu
              anchorEl={resumeAnchorEl}
              open={Boolean(resumeAnchorEl)}
              onClose={handleResumeMenuClose}
            >
              {resumeItems.map(({ id, label }) => (
                <MenuItem 
                  key={id}
                  component={ScrollLink}
                  to={id}
                  smooth={true}
                  duration={400}
                  offset={-70}
                  onClick={handleResumeMenuClose}
                >
                  {label}
                </MenuItem>
              ))}
            </Menu>
            <Button 
              color="inherit"
              component={ScrollLink}
              to="contact"
              smooth={true}
              duration={400}
              offset={-70}
            >
              Contact
            </Button>
          </Box>

          {/* Mobile Menu Button */}
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      {/* Mobile Drawer */}
      <Drawer
        variant="temporary"
        anchor="right"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true, // Better open performance on mobile
        }}
      >
        {drawer}
      </Drawer>
    </>
  );
};

export default NavigationBar;
